* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
	@include setFonts(400);
}
body {
	width: 100%;
	height: 100vh;
	overflow: hidden;
}
a {
	text-decoration: none;
	color: $colorDark;
}
input {
	border: none;
	outline: none;
}
button {
	outline: none;
	border: none;
	background-color: transparent;
}
